  border-left: 0;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  background-color: #f2f2f2;
}
.stepper-btn-wrap .stepper-btn-up {
  padding-top: 3px;
}
.stepper-btn-wrap .stepper-btn-down {
  padding-top: 2px;
}
.stepper-btn-wrap a {
  display: block;
  height: 50%;
  overflow: hidden;
  line-height: 100%;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  color: #666;
  font-size: 10px;
}
.stepper-btn-wrap a:hover {
  background-color: #0070c4;
  color: white;
}
/* Select2 overrides */
.select2-drop-active {
  border: 1px solid #0070c4;
  border-top: none;
}
.select2-results .select2-highlighted {
  background-color: #0070c4;
}
.select2-drop {
  border: 1px solid #e1e1e1;
}
.select2-search input,
.select2-search-choice-close,
.select2-container .select2-choice abbr,
.select2-container .select2-choice .select2-arrow b {
  background-image: url(data:image/png;base64,) !important;
}
.select2-search input.select2-active,
.select2-more-results.select2-active,
.select2-container-multi .select2-choices .select2-search-field input.select2-active {
  background-image: url(data:image/gif;base64,) !important;
}
/* Retina-ize icons */
@media only screen and (-webkit-min-device-pixel-ratio: 1.5), only screen and (min-resolution: 2dppx) {
  .select2-search input,
  .select2-search-choice-close,
  .select2-container .select2-choice abbr,
  .select2-container .select2-choice .select2-arrow b {
    background-image: url(data:image/png;base64,) !important;
    background-repeat: no-repeat !important;
    background-size: 60px 40px !important;
  }
}
.ats-switch {
  border-color: #e1e1e1;
}
.bootstrap-datetimepicker-widget td.active,
.bootstrap-datetimepicker-widget td.active:hover {
  background-color: #0070c4;
}
/* Isotope */
.isotope {
  /* make the well swell */
}
.isotope .row {
  margin-left: 0;
  margin-right: 0;
}
.isotope .well {
  border-color: #eee;
}
.isotope .item h4 a {
  color: #333;
}
.isotope .badge {
  background-color: #323232;
}
.isotope .item img.image {
  max-width: 100%;
  margin-top: 9px;
}
.isotope .info {
  padding: 4px;
}
/* Isotope Transitions
------------------------------- */
.isotope,
.isotope .item {
  -webkit-transition-duration: 0.8s;
  -moz-transition-duration: 0.8s;
  -ms-transition-duration: 0.8s;
  -o-transition-duration: 0.8s;
  transition-duration: 0.8s;
}
.isotope {
  -webkit-transition-property: height, width;
  -moz-transition-property: height, width;
  -ms-transition-property: height, width;
  -o-transition-property: height, width;
  transition-property: height, width;
}
.isotope .item {
  -webkit-transition-property: -webkit-transform, opacity;
  -moz-transition-property: -moz-transform, opacity;
  -ms-transition-property: -ms-transform, opacity;
  -o-transition-property: top, left, opacity;
  transition-property: transform, opacity;
}
/* experimental block grid code, will not work in IE8  */
[class*="block-grid-"] {
  display: block;
  padding: 0;
  margin: 0 -0.55556rem;
}
[class*="block-grid-"] > li {
  display: block;
  height: auto;
  float: left;
  padding: 0 0.55556rem 1.11111rem;
}
.small-block-grid-3 > li {
  width: 33.33333%;
  list-style: none;
}
.small-block-grid-3 > li:nth-of-type(1n) {
  clear: none;
}
.small-block-grid-3 > li:nth-of-type(3n+1) {
  clear: both;
}
/* responsive media queries */
@media (max-width: 768px) {
  header h1 small {
    display: block;
  }
  header div.description {
    padding-top: 9px;
    padding-bottom: 4px;
  }
  .isotope .item {
    position: static !important;
    -webkit-transform: translate(0px, 0px) !important;
    -moz-transform: translate(0px, 0px) !important;
    transform: translate(0px, 0px) !important;
  }
}
/* MEDIA QUERIES */
@media (max-width: 767px) {
  .xs-text-left {
    text-align: left;
  }
  .xs-text-right {
    text-align: right;
  }
  .activity {
    display: inline-block !important;
  }
  .footer {
    padding: 20px 0;
  }
  .footer .social {
    text-align: left;
    padding-top: 20px;
  }
}
